'use client';

import { JSX } from 'react';

// 物件用的型別宣告
interface Data {
  x?: number;
  y?: number;
}

// Props用的
interface ChildProps {
  title?: string;
  price?: number;
  isConnected?: boolean;
  aa?: number[];
  oa?: Data;
  sum?: (a: number, b: number) => number;
  jsx?: JSX.Element; // 用ReactNode限制會變更寬廣，JSX.Element會限制只能用React的JSX語法寫的元素
}

// 子女元件透過函式的傳入參數值(只會有一個，稱為props，是一個物件)
// 得到所有從父母元件傳給它的值
// 通常會使用"函式傳入參數解構"+"指定預設值"語法，先針對這props物件進行解構，讓屬性成為這元件的變數
export default function Child({
  title = '預設標題',
  price = 0,
  isConnected = false,
  aa = [],
  oa = {},
  sum = (a: number, b: number) => a + b,
  jsx = <p>hello</p>,
}: ChildProps) {
  // console.log(props);

  return (
    <>
      <h3>Child(子女元件)</h3>
      <p>title={title}</p>
      <p>price={price}</p>
      <p>isConnected={JSON.stringify(isConnected)}</p>
      <p>aa={JSON.stringify(aa)}</p>
      <p>oa={JSON.stringify(oa)}</p>
      <p>sum={sum(1, 2)}</p>
      <p>jsx={jsx}</p>
    </>
  );
}
