'use client';

// 導入自訂的元件
import Child from './child';

export default function Parent() {
  return (
    <>
      <h2>Parent(父母元件)</h2>
      {/* 渲染自訂的元件 */}
      {/* 父母與子女間的關係，是由 "父母" 渲染 "子女" 來決定 */}
      {/* 父母元件可以利用類似HTML的屬性值，指定值傳到子女元件，但不限於字串值 */}
      <Child
        title="今天在上react課"
        price={100}
        isConnected={true}
        aa={[1, 2, 3]}
        oa={{ x: 1, y: 2 }}
        sum={(a, b) => a + b}
        jsx={<p>hello</p>}
      />
      {/* 當沒有給定任何屬性時，會套用預設屬性值 */}
      <Child />
    </>
  );
}
